import React from 'react';
import logo from './logo.svg';
import './App.css';
import Card from 'react-bootstrap/Card'
import {BrowserRouter as Router, Link, Switch, Route } from 'react-router-dom';

function Title(){
  return(
    <h1 className="text-center py-3"> The Details of Employees of one-Tech</h1>
  )
}

function Director()
{
  return(
    <div>Director's side</div>
  )
}

function ProjectManger()
{
  return(
    <div>ProjectManger's side</div>
  )
}

function TeamLeader()
{
  return(
    <div>TeamLeader's side</div>
  )
}

function Trainee()
{
  return(
    <div>Trainee's side</div>
  )
}

function ReactModule()
{
  return(
    <div>React Framework</div>
  )
}

function Angular()
{
  return(
    <div>Angular Framework</div>
  )
}

function Net()
{
  return(
    <div>.net Coding</div>
  )
}

function Php()
{
  return(
    <div>php Coding</div>
  )
}

function DetailPrefix(){
  return(
    <div>Hello from:</div>
  )
}

function App() {
  return (
    <Card bg='info'>
    <div>
       <Router>
      <Card.Header className='d-flex justify-content-center align-items-center mx-auto'>
     
        
    <nav className="w-50 d-flex justify-content-between align-items-center">
      <Link to='/director' className='text-white'>Director</Link>
      <Link to='/project-Manager' className='text-white'>Project Manager</Link>
      <Link to='/team-leader' className='text-white'>Team Leader</Link>
      <Link to='/trainee' className='text-white'>Trainee</Link>
     
    </nav>  
  
    </Card.Header>
  
    <div className='text-center'>
    <Link to='/' className="text-white">Refresh</Link>  
    <Switch>
      <Route path='/director'><DetailPrefix/><Director/></Route>
      <Route path='/project-Manager'><DetailPrefix/><ProjectManger/></Route>
      <Route path='/team-leader'><DetailPrefix/><TeamLeader/></Route>
      <Route path='/trainee'><DetailPrefix/><Trainee/></Route>
      <Route Exact path='/'></Route>
    </Switch>
    
    </div> 
  
    <div className='text-center'>
    <Switch>
      <Route exact path='/react'><DetailPrefix/><ReactModule/></Route>
      <Route path='/angular'><DetailPrefix/><Angular/></Route>
      <Route path='/.net'><DetailPrefix/><Net/></Route>
      <Route path='/php'><DetailPrefix/><Php/></Route>
    </Switch>
    </div> 
    <Card.Footer className='d-flex justify-content-center align-items-center mx-auto'>     
       {/* The Details of Employees of one-Tech */}   
    <nav className="w-50 d-flex justify-content-between align-items-center">
      <Link to='/react' className='text-white'>React</Link>
      <Link to='/angular' className='text-white'>Angular</Link>
      <Link to='/.net' className='text-white'>.Net</Link>
      <Link to='/php' className='text-white'>Php</Link>
    </nav>  
  
    </Card.Footer>
  
    </Router>  
  
    </div>
    </Card>
  );
}

export default App;
